<h2 id="switch-开关" tabindex="-1"><a class="header-anchor" href="#switch-开关" aria-hidden="true">#</a> Switch 开关</h2>
<h3 id="用法" tabindex="-1"><a class="header-anchor" href="#用法" aria-hidden="true">#</a> 用法</h3>
<h4 id="基本用法" tabindex="-1"><a class="header-anchor" href="#基本用法" aria-hidden="true">#</a> 基本用法</h4>
<div class="component-box">
  <div class="component-box-top"><ClientOnly>
    <wd-switch
      v-model="isSwitchChecked"
      :loading="isSwitchLoading"
      @change="handleSwitchChange"
    />
    <wd-switch
      disabled
      size="large"
      :loading="true"
      v-model="isSwitchChecked"
    />
    </ClientOnly>
  </div>
  <div class="component-box-bottom">
    <div class="component-title">开关组件</div>
    <div class="component-desc">
      开关组件可以控制一个状态，当异步切换状态的时候可以显示loading状态；
    </div>
  </div>
</div>
